export default {
  data () {
    return {
      orderNumber: ''
    }
  },
  onLoad (options) {
    this.orderNumber = options.orderNumber
    wx.setNavigationBarTitle({ title: this.orderNumber ? '订单失败' : '订单成功' })
  },
  methods: {
    toHome () {
      wx.switchTab({ url: '/pages/home/main' })
    },
    toOrderDetail () {
      wx.navigateTo({ url: '/pages/order_detail/main' })
    }
  }

}
